.hero {
  width: 100%;
  min-height: 100vh;
  background: url("../../assest/night-background.jpg");
  background-size: cover;
  background-position: center;
  color: var(--text-white);
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-text {
  text-align: center;
  max-width: 800px;
}

.hero-text h1 {
  font-size: 60px;
  font-weight: 600;
}

.hero-text p {
  max-width: 700px;
  margin: 10px auto 20px;
  line-height: 1.4;
}
